import {
  Card,
  CardContent,
  CardActions,
  IconButton,
  InputAdornment,
  Typography,
  Stack,
} from "@mui/material";
import InputForm from "../components/InputForm";
import { IoSendSharp } from "react-icons/io5";
import Layout from "../components/layout";
import { useGetState, useSend } from "../utils/stateManger";
import { addMsg } from "../ContextApi/actions/AppActions";
import { useEffect } from "react";
import { useLocation, useParams } from "react-router-dom";

export default function Chat() {
  const { state, dispatch } = useGetState();
  const navigate = useLocation();

  const { sendMsg, sent } = useSend();

  useEffect(() => {
    //  navigate.state.tickit
    console.log(state.currntChat.chat);
  }, [sent]);
  return (
    <Layout>
      <Card>
        <CardContent>
          <Stack
            gap={1}
            sx={{ p: 0.5, minHeight: "350px", backgroundColor: "whitesmoke" }}
          >
            {state.currntChat.chat.map((msg) => (
              <Stack
                direction={"row"}
                justifyContent={
                  state.userData?.id == msg.sender ? "flex-end" : "flex-start"
                }
              >
                <Card sx={{ padding: 1 }}>{msg.msg}</Card>
              </Stack>
            ))}
          </Stack>
          <CardActions>
            <InputForm
              // disabled={loading}
              onChange={(e) => addMsg(e.target.value, dispatch)}
              value={state.msg}
              label="الرسالة"
              startAdornment={
                <InputAdornment position="start">
                  <IconButton onClick={() => sendMsg()} aria-label="send">
                    <IoSendSharp size={25} />
                  </IconButton>
                </InputAdornment>
              }

              //  onChange={(e) => addCountry(e.target.value, dispatch)}
            ></InputForm>
          </CardActions>
        </CardContent>
      </Card>
    </Layout>
  );
}
